import React, { useState } from "react";
import { NavLink } from "react-router-dom";
//Importation des icon
import { MdClose } from "react-icons/md";
import { FiMenu } from "react-icons/fi";

const Navbar = () => {
  const [navbarOpen, setNavbarOpen] = useState(false);

  const links = [
    {
      id: 1,
      path: "/",
      text: "Home",
    },
    {
      id: 2,
      path: "/about",
      text: "About",
    },
  ];

  const handleToggle = () => {
    // setNavbarOpen((prev) => !prev);
    setNavbarOpen(!navbarOpen); // Mise à jour automatique de l'état le navbarOpen (ligne 5) selon true/false => (!navbarOpen) inverse l'état prédéfini
  };

  const closeMenu = () => {
    setNavbarOpen(false);
  };

  return (
    <nav className="navBar">
      <button onClick={handleToggle}>
        {navbarOpen ? (
          <MdClose style={{ color: "#fff", width: "40px", height: "40px" }} />
        ) : (
          <FiMenu style={{ color: "#7b7b7b", width: "40px", height: "40px" }} />
        )}
      </button>
      {/*Utilisation de la class "MenuNav" en permanence  / Quand NavBarOpen == true, la class "showMenu" est ajoutée */}
      <ul className={`menuNav ${navbarOpen ? " showMenu" : ""}`}>
        {links.map((link) => {
          return (
            <li key={link.id}>
              {/* <Link to={/}>{Home}</Link>  - NavLink indique nativement class=active */}
              <NavLink to={link.path} onClick={() => closeMenu()}>
                {link.text}
              </NavLink>
            </li>
          );
        })}
      </ul>
    </nav>
  );
};

export default Navbar;
